import history from "../history";

export function jumpToFacebook() {
    window.open("https://www.facebook.com/Oim.Nogoon.Urlan", "_blank");
}

export function jumpToInstagram() {
    window.open("https://www.instagram.com/oim_nogoon_urlan/", "_blank");
}

export async function jumpToDepartments() {
    var url = "http://localhost:3000/";

    if (url === document.URL) {
        let interval = setInterval(function () {
            let height = document.getElementById("departments").offsetTop;
            if (height === null) return;
            clearInterval(interval);
            window.scrollTo({ top: height, behavior: "smooth" });
        }, 10);
    } else {
        history.push("/");
        jumpToDepartments();
    }
}
