import React, { useState } from "react";
import { useTheme } from "@material-ui/core/styles";
import {
    Grid,
    AppBar,
    Toolbar,
    CssBaseline,
    Fab,
    useMediaQuery,
    Link,
} from "@material-ui/core";

// Logo

import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import MenuIcon from "@material-ui/icons/Menu";

//css

import "./css/header.css";

// Function

import ScrollTop from "./function/ScrollTop";
import HideOnScroll from "./function/HideOnScroll";
import MobileHeader from "./function/MobileHeader";
import history from "./history";
import {
    jumpToFacebook,
    jumpToInstagram,
    jumpToDepartments,
} from "./function/JumpToSite";

// Image

import oimlogo from "../../../assets/Images/oim_logo.png";

function Header(props) {
    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("md"));
    const mobile = useMediaQuery(theme.breakpoints.up("sm"));

    const preventDefault = (event) => event.preventDefault();
    const [mobileMenu, setMobileMenu] = useState(false);

    return (
        <React.Fragment>
            <CssBaseline />
            <HideOnScroll {...props}>
                <AppBar position="fixed">
                    <Toolbar className="headerContainer">
                        {tablet ? (
                            <Grid container spacing={3}>
                                <Grid item xs={2}></Grid>
                                <Grid
                                    item
                                    xs={2}
                                    className="headerIconContainer"
                                >
                                    <img
                                        src={oimlogo}
                                        alt="logo"
                                        className="headerIcon"
                                        onClick={() => history.push(`/`)}
                                    />
                                </Grid>
                                <Grid item xs={1}></Grid>
                                <Grid
                                    item
                                    xs={6}
                                    className="headerLinkContainer"
                                >
                                    <Link
                                        onClick={() => history.push(`/`)}
                                        className="headerLink"
                                    >
                                        Нүүр хуудас
                                    </Link>
                                    <Link
                                        onClick={() =>
                                            history.push(`/ProductScreen`)
                                        }
                                        className="headerLink"
                                    >
                                        Бүтээгдэхүүн
                                    </Link>
                                    <Link
                                        onClick={preventDefault}
                                        className="headerLink"
                                    >
                                        Үйлчилгээ
                                    </Link>
                                    <Link
                                        onClick={preventDefault}
                                        className="headerLink"
                                    >
                                        Бидний тухай
                                    </Link>
                                    <Link
                                        onClick={() => jumpToDepartments()}
                                        className="headerLink"
                                    >
                                        Салбар
                                    </Link>
                                    <div>
                                        <FacebookIcon
                                            className="headerIcon"
                                            onClick={() => jumpToFacebook()}
                                        />
                                        <InstagramIcon
                                            className="headerIcon"
                                            onClick={() => jumpToInstagram()}
                                        />
                                    </div>
                                </Grid>
                                <Grid item xs={1}></Grid>
                            </Grid>
                        ) : mobile ? (
                            <Grid container spacing={3}>
                                <Grid item xs={1}></Grid>
                                <Grid
                                    item
                                    xs={2}
                                    className="headerIconContainer"
                                >
                                    <img
                                        src={oimlogo}
                                        alt="logo"
                                        className="headerIcon"
                                        onClick={() => history.push(`/`)}
                                    />
                                </Grid>
                                <Grid item xs={2}></Grid>
                                <Grid
                                    item
                                    xs={6}
                                    className="headerLinkContainer"
                                >
                                    <Link
                                        onClick={() => history.push(`/`)}
                                        className="headerLink"
                                    >
                                        Нүүр хуудас
                                    </Link>
                                    <Link
                                        onClick={() =>
                                            history.push(`/ProductScreen`)
                                        }
                                        className="headerLink"
                                    >
                                        Бүтээгдэхүүн
                                    </Link>
                                    <Link
                                        onClick={preventDefault}
                                        className="headerLink"
                                    >
                                        Үйлчилгээ
                                    </Link>
                                    <MenuIcon
                                        style={{
                                            fill: "black",
                                            cursor: "pointer",
                                        }}
                                        onClick={() =>
                                            setMobileMenu(!mobileMenu)
                                        }
                                    />
                                    <MobileHeader mobileMenu={mobileMenu} />
                                </Grid>
                                <Grid item xs={1}></Grid>
                            </Grid>
                        ) : (
                            <Grid container spacing={3}>
                                <Grid
                                    item
                                    xs={4}
                                    className="headerIconContainer"
                                >
                                    <img
                                        src={oimlogo}
                                        alt="logo"
                                        className="headerIcon"
                                        style={{ height: "65%" }}
                                        onClick={() => history.push(`/`)}
                                    />
                                </Grid>
                                <Grid item xs={2}></Grid>
                                <Grid
                                    item
                                    xs={6}
                                    style={{
                                        width: "100%",
                                        justifyContent: "flex-end",
                                    }}
                                    className="headerLinkContainer"
                                >
                                    <MenuIcon
                                        style={{ fill: "black" }}
                                        onClick={() =>
                                            setMobileMenu(!mobileMenu)
                                        }
                                    />
                                    <MobileHeader mobileMenu={mobileMenu} />
                                </Grid>
                            </Grid>
                        )}
                    </Toolbar>
                </AppBar>
            </HideOnScroll>
            <Toolbar id="back-to-top-anchor" />
            <ScrollTop {...props}>
                <Fab
                    style={{ background: "#82d900" }}
                    size="small"
                    aria-label="scroll back to top"
                >
                    <ArrowUpwardIcon />
                </Fab>
            </ScrollTop>
        </React.Fragment>
    );
}

export default Header;
