import React, { lazy, Suspense } from "react";
import { Router, Switch, Route } from "react-router-dom";

import Loading from "./HomeScreen/Components/Loading";
import history from "./HomeScreen/Components/history";
import Header from "./HomeScreen/Components/Header";
import Footer from "./HomeScreen/Components/footer";

const HomeScreen = lazy(() =>
    import(/*webpackChunkName: "HomeScreen" */ "./HomeScreen/index")
);

const ProductScreen = lazy(() =>
    import(/*webpackChunkName: "ProductScreen" */ "./Products/index")
);

function App() {
    return (
        <div style={{ background: "white", overflow: "hidden" }}>
            <Router history={history}>
                <Header />
                <Suspense fallback={<Loading />}>
                    <Switch>
                        <Route exact path={"/"} component={HomeScreen} />
                        <Route
                            exact
                            path={"/ProductScreen"}
                            component={ProductScreen}
                        />
                    </Switch>
                </Suspense>
                <Footer />
            </Router>
        </div>
    );
}

export default App;
