import React from "react";
import { useTheme } from "@material-ui/core/styles";
import {
    Slide,
    Grid,
    useMediaQuery,
    Link,
    useScrollTrigger,
} from "@material-ui/core";
import PropTypes from "prop-types";
import history from "../history";
import {
    jumpToFacebook,
    jumpToInstagram,
    jumpToDepartments,
} from "./JumpToSite";

// Icon

import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";

function MobileHeader(props) {
    const isMobile = props.mobileMenu;
    const { window } = props;

    const preventDefault = (event) => event.preventDefault();

    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("md"));
    const mobile = useMediaQuery(theme.breakpoints.up("sm"));

    const trigger = useScrollTrigger({ target: window ? window() : undefined });

    if (tablet === false && mobile === true) {
        return (
            <Slide
                appear={false}
                direction="left"
                in={isMobile ? (trigger ? false : true) : false}
                style={{ top: 60, right: 0, minHeight: 100 }}
            >
                <Grid container className="mobileHeaderLinkContainer">
                    <Grid item xs={12}>
                        <Link onClick={preventDefault} className="headerLink">
                            Бидний тухай
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            onClick={() => jumpToDepartments()}
                            className="headerLink"
                        >
                            Салбар
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <FacebookIcon
                            className="headerIcon"
                            onClick={() => jumpToFacebook()}
                        />
                        <InstagramIcon
                            className="headerIcon"
                            onClick={() => jumpToInstagram()}
                        />
                    </Grid>
                </Grid>
            </Slide>
        );
    } else if (tablet === false && mobile === false) {
        return (
            <Slide
                appear={false}
                direction="left"
                in={isMobile ? (trigger ? false : true) : false}
                style={{ top: 58, right: 0, minHeight: 240 }}
            >
                <Grid container className="mobileHeaderLinkContainer">
                    <Grid item xs={12}>
                        <Link
                            onClick={() => history.push(`/`)}
                            className="headerLink"
                        >
                            Нүүр хуудас
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            onClick={() => history.push(`/ProductScreen`)}
                            className="headerLink"
                        >
                            Бүтээгдэхүүн
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link onClick={preventDefault} className="headerLink">
                            Үйлчилгээ
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link onClick={preventDefault} className="headerLink">
                            Бидний тухай
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            onClick={() => jumpToDepartments()}
                            className="headerLink"
                        >
                            Салбар
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <FacebookIcon
                            className="headerIcon"
                            onClick={() => jumpToFacebook()}
                        />
                        <InstagramIcon
                            className="headerIcon"
                            onClick={() => jumpToInstagram()}
                        />
                    </Grid>
                </Grid>
            </Slide>
        );
    } else return null;
}

MobileHeader.propTypes = {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
};

export default MobileHeader;
